<template lang="pug">

#horario
	Title(v-if="this.$route.params.titulo === undefined" :t="carrera.replace(/([A-Z])/g, ' $1')" :s="sems[horario.data.sem]" i="format-list-numbers")
	Title(v-else :t="this.$route.params.titulo" :s="sems[horario.data.sem]" i="format-list-numbers")

	transition(name="slide-fade" mode="out-in")
		.horario(v-if="!$store.state.loading")

			.container-card.level
				.level-left
					.level-item.is-size-5
						b-icon(icon="alert-circle-outline")
						span
							| Horario correspondiente a 
							b {{horario.data.year}}
							|, pueden existir cambios

			fieldset.field-a.container-card(v-for="(a,index) in horario.schedule")
				legend.bg--main.text-pure-paper.box-shadow-S {{anios[index]}}
				fieldset.field-s(v-for="(d,index2) in a")
					legend.bg-pure-paper.text-main {{dias[index2]}}
					table(v-for="(t, index3) in d")
						tr
							th.bg-pure-paper.text-ink Turno {{turnos[index3]}}
						tr(v-for="(b, index4) in t")
							|De {{b.f}} a {{b.t}} - <b>{{b.a}}</b> - Aula {{b.b}} - {{b.e}}


//
	transition(name="slide-fade" mode="out-in")
		.pde(v-if="!$store.state.loading")
			fieldset.field-a.container-card(v-for="(a,index) in planExp")
				legend.bg--main.text-pure-paper.box-shadow-S {{anios[index-1]}}
				fieldset.field-s(v-for="(s,index2) in a")
					legend.bg-pure-paper.text-main {{sems[index2]}}
					table.p
						Asignatura(v-for="(m,index3) in s" :i="parseInt(index3)" :m="m")


</template>
<script>
import axios from 'axios'
//import Asignatura from '@/components/Asignatura.vue'
import Title from '@/components/Title.vue'

export default {
  name: 'Plan',
  data() {
  	return {
  		horario: {},
  		dias: ['Lunes','Martes','Miércoles','Jueves','Viernes','Sábado','Domingo'],
  		turnos: ['mañana', 'tarde'],
  		anios: ['Primer año','Segundo año','Tercer año','Cuarto año','Quinto año','Sexto año','Séptimo año'],
  		sems: { 1: 'Primer semestre', 2: 'Segundo semestre', 'Anual': 'Anual' }
  	}
  },
  components: {
    //Asignatura,
    Title
  },
  props: {
  	carrera: String,
  	facultad: String
  },
  methods:{
  },
  beforeMount(){
    this.$store.commit('startLoading')
  },
  mounted() {
  	axios
  		.get("/assets/json/horarios/"+this.facultad+"_"+this.carrera+".json")
  		.then(response => (this.horario = response.data, this.$store.commit('stopLoading')))
  }
}

</script>
<style lang="stylus">

@import "../assets/styles/variables.styl";

.field-a, .field-s

	legend
		font-size: 12px
		font-weight: bold
		border-radius: 4px
		text-transform: uppercase

	padding: 0 8px
	
	

.field-a

	legend
		padding:4px 10px

	margin: 0 -12px 12px
	border: none
	border-radius: 6px


.field-s 

	legend
		padding:2px 8px

	margin: 12px -8px
	border: none
	border-top: 1px alpha(color-main, 10%) solid
	

table.p
	th
		vertical-align:middle
		font-size:12px
		text-transform: uppercase
	td
		padding: 6px 0
	.i
		width: 8%
		text-align:center
		font-weight:bold
	.n
		width: 52%
	.c
		.r
			font-weight:bold
			padding: 0 4px
		width: 20%
		text-align:center
	.c2
		width: 40%
		text-align:center
	
	width:100%

</style>